 
import React, { useEffect, useState } from 'react'
import Box from '@mui/material/Box';
import { styled } from '@mui/material/styles';
import Paper from '@mui/material/Paper';
import { Button, Fab, Grid, Typography } from '@mui/material';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import GroupAddIcon from '@mui/icons-material/GroupAdd'; 
import PeopleAltIcon from '@mui/icons-material/PeopleAlt';
import PaidIcon from '@mui/icons-material/Paid';
import SendIcon from '@mui/icons-material/Send';
import Stack from '@mui/material/Stack';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import { NavLink } from 'react-router-dom';
import { config } from './Config/Config';
import axios from 'axios';
import ButtonCommon from './ButtonCommon';


const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

const UserCenter = () => {

  const [profits, setProfit] =useState();
console.log(profits , "profit state user Center")
  const getProfit = () => {
    let url = config.baseURL + "/customer?id=8";
    console.log( url);

    axios
    .get(url)
      .then((response) => {
        if (response.status == 200) {
            console.log(response, " response profit user center"); 
            setProfit(response.data.response);
        }
      })
      .catch((e) => {
        console.log(e);
      });
  }; 

  useEffect(()=>{
    getProfit();
  }, [])
  return (
    <> 
    {profits?  <Box
      sx={{ 
        justifyContent:"center",
        alignItems:"center",
        maxHeight:"100vh",
        display: 'flex',
        flexWrap: 'wrap',
        '& > :not(style)': {
          m: 1,
          width: 900,
          // height: 600,
        },
      }}
    >
     
      <Paper elevation={9} >
      <Paper elevation={5}   sx={{
   mx:3, 
    borderRadius:"10px"
  }}>
      <Typography variant='h5' sx={{textAlign:"center", py:4,  my:3, }} fontFamily= "Audiowide" sans-serif>
        User Center
      </Typography>
      </Paper> 

      <Box sx={{ flexGrow: 1, my:5 }}>
      <Grid container spacing={2}>
        <Grid item xs={6}>
          <Item>
          <Typography sx={{color: "blue"}} fontFamily= "Audiowide" sans-serif>
              {profits.profit}
            </Typography>
            <Typography fontFamily= "Audiowide" sans-serif>
               Personal Income
            </Typography>
          </Item>
        </Grid>
        <Grid item xs={6}>
          <Item>
          <Typography sx={{color: "blue"}} fontFamily= "Audiowide" sans-serif>
          {JSON.parse(profits.profit) + JSON.parse(profits.asset) } 
              </Typography>
              <Typography fontFamily= "Audiowide" sans-serif>
              Available Balance
              </Typography>
          </Item>
        </Grid> 
      </Grid>
    </Box>

    {/* <Box sx={{ flexGrow: 1, my:3 }}>
      <Grid container spacing={2}>
        <Grid item xs={6}>
          <Item> 
            <Typography>
               Membership Lavel
            </Typography>
          </Item>
        </Grid>
        <Grid item xs={6}>
          <Item> 
          <Button variant="contained"  sx={{borderRadius:"20px", height:"25px",px:"25px"}}>More</Button>
          </Item>
        </Grid> 
      </Grid>
    </Box>  */}

      <Paper elevation={0} sx={{m:"40px 40px 40px 40px", display:"flex", justifyContent:"space-between" }} fontFamily= "Audiowide" sans-serif>  
      <NavLink to="/recharge"><Fab variant="extended" size='small' sx={{px:"30px"}}  ><Typography fontFamily= "Audiowide" sans-serif>Recharge</Typography>  </Fab></NavLink>
     <NavLink to="/withdraw"> <Fab variant="extended" size='small' sx={{px:"30px"}} ><Typography fontFamily= "Audiowide" sans-serif>Withdraw</Typography>  </Fab></NavLink>
      </Paper> 

      <NavLink to="/teamreport" style={{ textDecoration: 'none' }}> 
    <Paper elevation={0} sx={{m:"20px 40px 0px 40px", display:"flex", justifyContent:"space-between", }} >   
    <Stack container direction="row" spacing={2} sx={{width:"100%", }}>
      <Button variant="contained" sx={{width:"100%",justifyContent:"start", borderRadius:"20px", backgroundColor:"#E0E0E0", '&:hover': {
    backgroundColor:"#E0E0E0",
  }, }}>
      <PeopleAltIcon sx={{color:"blue"}}/> <Typography  sx={{ml:1, color:"black"}} fontFamily= "Audiowide" sans-serif> Team/Member income </Typography> 
      </Button>
     
      <div style={{marginLeft:"-40px", marginTop:"5px", zIndex:"2"}}><KeyboardArrowRightIcon sx={{  }} /></div>
    </Stack>
      </Paper>
      </NavLink>
   <NavLink to="/sharecode" style={{ textDecoration: 'none' }}> 
   <Paper elevation={0} sx={{m:"20px 40px 0px 40px", display:"flex", justifyContent:"space-between" }} >   
    <Stack container direction="row" spacing={2} sx={{width:"100%",}}>
      <Button variant="contained" sx={{width:"100%",justifyContent:"start" , borderRadius:"20px", backgroundColor:"#E0E0E0", '&:hover': {
    backgroundColor:"#E0E0E0",
  }, }}>
      <GroupAddIcon sx={{color:"blue"}} /><Typography  sx={{ml:1, color:"black"}} fontFamily= "Audiowide" sans-serif> Invite Friends </Typography> 
      </Button>
      <div style={{marginLeft:"-40px", marginTop:"5px", zIndex:"2"}}><KeyboardArrowRightIcon  sx={{  }}/></div>
    </Stack>
      </Paper>
      </NavLink>
      <NavLink to="/activities" style={{ textDecoration: 'none' }}>
    <Paper elevation={0} sx={{m:"20px 40px 0px 40px", display:"flex" }} >   
    <Stack container direction="row" spacing={2} sx={{width:"100%",}}>
      <Button variant="contained" sx={{width:"100%", justifyContent:"start", borderRadius:"20px", backgroundColor:"#E0E0E0", '&:hover': {
    backgroundColor:"#E0E0E0",
  }, }}>
      <PaidIcon sx={{color:"blue" }} /><Typography  sx={{ml:1, color:"black",}} fontFamily= "Audiowide" sans-serif>Activities </Typography> 
      </Button>
      <div style={{marginLeft:"-40px", marginTop:"5px", zIndex:"2"}}><KeyboardArrowRightIcon  sx={{  }}/></div>
    </Stack>
      </Paper>
      </NavLink>

    <ButtonCommon />
    {/* <Paper elevation={0}  sx={{ my:6 }} >   
      <Button  variant="outlined"  sx={{ borderRadius:"20px", width:"87%", ml:5}} fontFamily= "Audiowide" sans-serif>Sign out</Button>
      </Paper>   */}
      </Paper> 
    </Box>  :null}
    
    </>
  )
}

export default UserCenter