import React, { useState, useEffect } from "react";
import { config } from "../../config";
import "../../Styles/AdminMain.css"
import "../../Styles/AdminRightSection.css"
import AdminSideBar from './AdminSideBar'
import ActivityTable from './ActivityTable'
const AdminRightParent = (props) => {
    let [data, setdata] = useState([]);
    // let type = 1;
    const getdata = () => {
        fetch(config.baseURL + props.url)
            // fetch("https://jsonplaceholder.typicode.com/users")
            .then((res) => res.json())
            .then((json) => {

                let newData = json.response.filter(checktype);
                setdata((data = newData));
                console.log(json.response, "items form ");
                console.log(newData, "newdata")
            });


    };
    const checktype = (dataraw) => {
        if (dataraw.activity_type == props.type) {
            return true
        } else return false
    }
    useEffect(() => {

        getdata();
    }, []);
    return (
        <div className="tableListingClass" style={{ display: "flex" }}>
            <div className='' style={{ width: "25%" }}><AdminSideBar bIndex={props.active} /></div>

            <div className='adminRightRoot'>
                <center>
                    {props.title}
                </center>



                <div className="tableClass"><ActivityTable rows={data} check="1" /></div>
            </div>
        </div>
    )
}

export default AdminRightParent
