import React from 'react'
import "../../Styles/AdminSideBar.css"
import { NavLink } from 'react-router-dom'
import { useNavigate } from 'react-router-dom'
import Cookies from "js-cookie";

const AdminSideBar = (props) => {
    let navigate = useNavigate()
    const signout = () => {
        Cookies.remove("asdae3wd3ew");
        navigate("/admin/login")
    }
    return (
        <div style={{ display: "flex", color: "white" }}>
            <div style={{ width: "100%", backgroundColor: "#1C2536", height: "50rem", padding: "10px 10px" }}>
                <center>
                    <div>
                        {/* </center>  */}
                        <NavLink to="/Admin" style={{ textDecoration: "none" }}>
                            <b>Admin</b>
                        </NavLink></div>
                    <div >Investment Coin</div>
                </center>
                <div >
                    <NavLink to="/Admin/deposit" style={{ textDecoration: "none" }}><div className={`${props.bIndex == 1 ? `sidebarItemActive` : `sidebarItem`}  `}>Deposit</div></NavLink>
                    <NavLink to="/Admin/withdraw" style={{ textDecoration: "none" }}><div className={`${props.bIndex == 2 ? `sidebarItemActive` : `sidebarItem`}  `}>WithDraw</div></NavLink>
                    <NavLink to="/Admin/ActivityView/19" style={{ textDecoration: "none" }}><div className={`${props.bIndex == 3 ? `sidebarItemActive` : `sidebarItem`}  `}>Customer</div></NavLink>
                    {/* <NavLink to="/Admin/ActivityView/19" style={{ textDecoration: "none" }}> */}
                    <div className={`sidebarItem `} onClick={signout}><center>Log out</center > </div>
                    {/* </NavLink> */}


                </div>
            </div >
        </div >
    )
}

export default AdminSideBar
