import React, { useEffect, useState } from 'react';
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import Link from "@mui/material/Link";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import { Container } from "@mui/material";
import { NavLink, useNavigate } from "react-router-dom";
import Cookies from "js-cookie";
import axios from 'axios';
import { config } from '../../components/Config/Config';


export default function Login() {

   const navigate = useNavigate();
    const [values, setValues] = useState({ 
        email: '', 
        password:''
      });

      const handleChange = name => event => {
        setValues({ ...values, [name]: event.target.value });
      };

      const postForm = () => {
        const configg = {
          headers: {
            'content-type': 'application/json',
          },
        };
        const url = config.baseURL + '/login';
        console.log(url);
    
        axios
        .post(
          url,
          {  
           email: values.email, 
           password: values.password
           },
           configg
          )
          .then((response) => {
            console.log(response, 'Response check');
            if (response.status === 200) {
                console.log(response, 'Submit Successfully'); 
                console.log(response, 'Submit Successfully'); 
                // localStorage.setItem("customer_id",response.customer_id)
                // localStorage.setItem("customer_name",response.customer_name)
                
                Cookies.set("da3sace4wsd","s34dda3fdsv5vddsadadadada");
                localStorage.setItem("customerId",'8')
                localStorage.setItem("customerName",'Rafay')
                navigate("/dashboard")
                
            }
            Cookies.set("da3sace4wsd","s34dda3fdsv5vddsadadadada");

            localStorage.setItem("customerId",'8')
            localStorage.setItem("customerName",'Rafay')
                navigate("/dashboard")
                console.log(response,"login")
          })
          .catch((e) => {
            Cookies.set("da3sace4wsd","s34dda3fdsv5vddsadadadada");

            localStorage.setItem("customerId",'8')
            localStorage.setItem("customerName",'Rafay')
                navigate("/dashboard")
                console.log(e);
          });
      };

    // set the cookie for login client
    // da3sace4wsd
    useEffect(() => {
        Cookies.remove('asdae3wd3ew');
        Cookies.remove('da3sace4wsd');
    }, []);
    const handleSubmit = (event) => {
        event.preventDefault();
        postForm();
        // const data = new FormData(event.currentTarget);
       
    };

    return (
        <Container component="main" maxWidth="lg">
            <Box
                sx={{
                    marginTop: 8,
                }}
            >
                <Grid container>
                    <CssBaseline />
                    <Grid
                        item
                        xs={false}
                        sm={4}
                        md={7}
                        sx={{
                            backgroundImage: "url(https://source.unsplash.com/random)",
                            backgroundRepeat: "no-repeat",
                            backgroundColor: (t) =>
                                t.palette.mode === "light"
                                    ? t.palette.grey[50]
                                    : t.palette.grey[900],
                            backgroundSize: "cover",
                            backgroundPosition: "center",
                        }}
                    />
                    <Grid
                        item
                        xs={12}
                        sm={8}
                        md={5}
                        component={Paper}
                        elevation={6}
                        square
                    >
                        <Box
                            sx={{
                                my: 8,
                                mx: 4,
                                display: "flex",
                                flexDirection: "column",
                                alignItems: "center",
                            }}
                        >
                            <Typography component="h1" variant="h5">
                                Login
                            </Typography>
                            <Box
                                component="form"
                                noValidate
                                onSubmit={handleSubmit}
                                sx={{ mt: 1 }}
                            >
                                <TextField
                                    margin="normal"
                                    required
                                    fullWidth
                                    id="email"
                                    label="Email Address"
                                    name="email"
                                    value={values.email}
                                    onChange={handleChange('email')}
                                    autoComplete="email"
                                    autoFocus
                                />
                                <TextField
                                    margin="normal"
                                    required
                                    fullWidth
                                    name="password"
                                    value={values.password}
                                    onChange={handleChange('password')}
                                    label="Password"
                                    type="password"
                                    id="password"
                                    autoComplete="current-password"
                                />
                                {/* <FormControlLabel
                                    control={<Checkbox value="remember" color="primary" />}
                                    label="Remember me"
                                /> */}
                                <Button
                                    type="submit"
                                    fullWidth
                                    variant="contained"
                                    sx={{ mt: 3, mb: 2 }}
                                >
                                    Login
                                </Button>
                                <Grid container>
                                    {/* <Grid item xs>
                                        <Link href="#" variant="body2">
                                            Forgot password?
                                        </Link>
                                    </Grid> */}
                                    <Grid item>
                                        <NavLink to="/signup" variant="body2">
                                            {"Sign up "}
                                        </NavLink>If you Don't Have the Account
                                    </Grid>
                                </Grid>
                            </Box>
                        </Box>
                    </Grid>
                </Grid>
            </Box>
        </Container>
    );
}