import React, { useState, useEffect } from "react";
import { config } from "../../config";
import "../../Styles/AdminMain.css"
import "../../Styles/ActivityView.css"
// const axios = require("axios").default;
import axios from 'axios'
import AdminSideBar from './AdminSideBar'
import ActivityTable from './ActivityTable'
import { useParams } from "react-router";
import Button from '@mui/material/Button';
// import DeleteIcon from '@mui/icons-material/Delete';
import CancelIcon from '@mui/icons-material/Cancel';
import DoneAllIcon from '@mui/icons-material/DoneAll';
import SendIcon from '@mui/icons-material/Send';
import Stack from '@mui/material/Stack';
const ActivityView = (props) => {
    let { id } = useParams();
    let [data, setdata] = useState([]);
    let [state, setstate] = useState({
        activityStatus: "null",
        message: "Autharized Person Allow to click button"
    });

    const getdata = () => {
        fetch(config.baseURL + "getactitivty?id=" + id)
            .then((res) => res.json())
            // fetch("https://jsonplaceholder.typicode.com/users")
            .then((json) => {

                setdata((data = json.response));
                console.log(json.response, "items form ");
            });
    };
    const updateActivity = (props) => {
        const configg = {
            headers: {
                "content-type": "application/json",
                // headers: {
                // "Access-Control-Allow-Origin": "*",
                // "Access-Control-Allow-Credentials": "true",
                // "Access-Control-Allow-Methods": "GET,HEAD,OPTIONS,POST,PUT",
                // "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept, Authorization"
                //   }
            },
        };
        let url = config.baseURL + "actitivty";
        console.log(props, "state.activityStatus")
        axios
            .put(url, {
                "activityId": id,
                "Status": props
            }, configg)
            .then((response) => {
                console.log(response, "in Avtivity update")
                if (response.status == 200) {
                    console.log("activity updated")
                    setstate({
                        ...state,
                        "message": response.apiName,
                    });
                } else if (response.status == 403) {
                    setstate({
                        ...state,
                        "message": response.apiName,
                    });
                } else {
                    console.log("activity updated failed")
                }
                console.log("added succesfully");
                console.log("History check");

            })
            .catch((e) => {
                setstate({
                    ...state,
                    "message": "Failed to update by system",
                });
                console.log("activity updated failed")
                console.log(e);
            });
    };
    const handleInputs = (props) => {

        // setstate({
        //     ...state,
        //     "activityStatus": e.target.value,
        // });
        // updateActivity()
    };
    useEffect(() => {

        getdata();
    }, []);
    return (
        <div className="tableListingClass" style={{ display: "flex" }}>
            <div className='' style={{ width: "25%" }}><AdminSideBar bIndex='3' /></div>

            <div className='adminRightRoot'>
                {/* <center>
                    Activity details
                </center> */}


                <div >
                    <div style={{ marginTop: "50px" }}><center><b>Activity details</b></center>
                        <div style={{ display: "flex" }}>
                            <div style={{ width: "150px" }}>Type<br />
                                Amount<br />
                                Status<br />
                                Create_date<br />
                            </div>
                            <div>
                                {data.activity_type == 1 ? "Deposit" : data.activity_type == 2 ? "Withdraw" : null}<br />
                                {data.amount}$<br />
                                {data.status == 1 ? "Request" : data.status == 2 ? "Approved" : "Declined"}<br />
                                {data.create_date}<br />
                            </div>

                        </div>
                    </div>


                    <div style={{ marginTop: "50px" }}><center><b>Customer details</b></center>
                        <div style={{ display: "flex" }}>
                            <div style={{ width: "150px" }}>
                                customer_name<br />
                                asset<br />
                                email<br />
                                phone_no<br />
                                profit
                            </div>
                            <div>
                                {data.customer_name}<br />
                                {data.asset}$<br />
                                {data.email}<br />
                                {data.phone_no}<br />
                                {data.profit}$
                            </div>

                        </div>
                    </div>
                    {
                        data.activity_type == 1 ?
                            <div style={{ marginTop: "50px" }}><center><b>ScreenShot Image</b></center>
                                <center>
                                    <div style={{ display: "flex" }}>
                                        <img src={config.baseURL + "images/deposit/" + data.img} alt="SS image missing" />
                                    </div>
                                </center>
                            </div> : null
                    }
                    {data.status == 1 ? <div className="ActivityAction"  >
                        <Stack sx={{
                            // textAlign: 'center',
                            justifyContent: 'center'
                        }} direction="row" spacing={2}>
                            <Button onClick={(e) => updateActivity(3)} value="3" variant="outlined" startIcon={<CancelIcon />}>
                                Declined
                            </Button>
                            <Button onClick={(e) => updateActivity(2)} variant="contained" value="2" endIcon={<DoneAllIcon />}>
                                Approved
                            </Button>
                        </Stack>
                        <div style={{ marginTop: "50px" }}>

                            <center>
                                <span style={{ color: "red" }}> {state.message}</span>
                            </center>
                        </div>
                    </div> : null
                    }

                </div>
                {/* <div className="tableListingClass"><ActivityTable /></div> */}
            </div>
        </div>
    )
}

export default ActivityView
