import React, { useEffect } from 'react';
import { NavLink } from 'react-router-dom';
import Cookies from "js-cookie";
// import { CleanCookies } from "../utils/auth/cleanCookies"
const NotFound = () => {
    useEffect(() => {
        Cookies.remove('asdae3wd3ew');
        Cookies.remove('da3sace4wsd');
    }, []);
    return (
        <div style={styles.container}>
            <h1 style={styles.heading}>404 Error: Page Not Found</h1>
            <p style={styles.message}>The requested page does not exist.
                <NavLink to="/login">Go to Login</NavLink></p>
        </div>
    );
};

const styles = {
    container: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        minHeight: '100vh',
        backgroundColor: '#f8f8f8',
        fontFamily: 'Arial, sans-serif',
    },
    heading: {
        fontSize: '2.5rem',
        fontWeight: 'bold',
        marginBottom: '1rem',
        color: '#333',
    },
    message: {
        fontSize: '1.2rem',
        color: '#666',
    },
};

export default NotFound;
