import React, { useEffect, useState } from "react";
import Box from "@mui/material/Box";
import { styled } from "@mui/material/styles";
import Paper from "@mui/material/Paper";
import { Button, Fab, Grid, Typography } from "@mui/material";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import GroupAddIcon from "@mui/icons-material/GroupAdd";
import PeopleAltIcon from "@mui/icons-material/PeopleAlt";
import PaidIcon from "@mui/icons-material/Paid";
import SendIcon from "@mui/icons-material/Send";
import Stack from "@mui/material/Stack";
import KeyboardArrowRightIcon from "@mui/icons-material/KeyboardArrowRight";
import KeyboardBackspaceIcon from "@mui/icons-material/KeyboardBackspace";
import ButtonBase from "@mui/material/ButtonBase"; 
import ContentCopyIcon from '@mui/icons-material/ContentCopy'; 
import Img from "../Images/cripto.jpg";
import { NavLink } from "react-router-dom";
import axios from "axios";
import { config } from "./Config/Config";
import ButtonCommon from "./ButtonCommon";

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === "dark" ? "#1A2027" : "#fff",
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: "center",
  color: theme.palette.text.secondary,
}));

const ShareCode = () => {

  const [profits, setProfit] =useState();
  console.log(profits , "share code state")
 
    const getProfit = () => {
      let url = config.baseURL + "/customer?id=8";
      console.log( url);
  
      axios
      .get(url)
        .then((response) => {
          if (response.status == 200) {
              console.log(response, "response share code");
              // console.log(response.data, "get profit");
              setProfit(response.data.response);
          }
        })
        .catch((e) => {
          console.log(e);
        });
    };  

    const InviteLink = "http://localhost:5100/signin/"
  // console.log(InviteLink, "InviteLink checkkkkkkkkkkkkkkkkkkk")

    useEffect(()=>{
      getProfit();
    }, [])

  return (
    <>
    {profits?  <Box
        sx={{
          justifyContent: "center",
          alignItems: "center",
          maxHeight: "100vh",
          display: "flex",
          flexWrap: "wrap", 
          "& > :not(style)": {
            m: 1, 
            width: 900,
            // height: 600,
            paddingBottom:5
          },
        }}
      >
        <Paper elevation={9}>
          <Paper
            elevation={0}
            sx={{
              p: 2,
              margin: "auto",
              // maxWidth: 900,
              flexGrow: 1,
              backgroundColor: (theme) =>
                theme.palette.mode === "dark" ? "#1A2027" : "#fff",
            }}
          >
            <Grid container spacing={2} sx={{ mt: 2 }}>
              <Grid item xs={1} sm={1} md={1} lg={1}>
                <ButtonBase>
                  <NavLink to="/usercenter"><KeyboardBackspaceIcon /></NavLink>
                  
                </ButtonBase>
              </Grid>
              <Grid item xs={11} sm={11} md={11} lg={11} sx={{ textAlign: "center" }}> 
              <ButtonBase>
                  <Typography variant="h5" >
                    Share Code
                  </Typography> 
              </ButtonBase>
              </Grid>
            </Grid>
          </Paper>

       
   <Paper
            elevation={0}
            sx={{
                borderBottom: '1px solid #AEC2B6',
                textAlign: 'center', 
              p: 2,
              margin: "auto",
              // maxWidth: 900,
              flexGrow: 1,
              backgroundColor: (theme) =>
                theme.palette.mode === "dark" ? "#1A2027" : "#fff",
            }}
          >
       <Box
        component="img"
        sx={{ 
          height: 350,
          width: 1000,
          maxHeight: { xs: 233, md: 400 },
          maxWidth: { xs: 350, md: 800 },
        }}
        alt="The house from the offer."
        src={Img} 
          />
          </Paper>

          <Paper
            elevation={0}
            sx={{
                borderBottom: '1px solid #AEC2B6',
              p: 2,
              margin: "auto",
              // maxWidth: 500,
              flexGrow: 1,
              backgroundColor: (theme) =>
                theme.palette.mode === "dark" ? "#1A2027" : "#fff",
            }}
          >
            <Grid container spacing={0} >
              <Grid item xs={4} sm={4} md={4} lg={4}>
                <ButtonBase>
                  <Typography variant="body1">Invite Code:</Typography>
                </ButtonBase>
              </Grid>
              <Grid item xs={8} sm={8} md={8} lg={8} sx={{ textAlign: "right" }}>
              <ButtonBase> 
                  <Typography variant="body1" > {profits?profits.invitaion_code:null}<ContentCopyIcon /> </Typography> 
              </ButtonBase>
              </Grid>
            </Grid>
          </Paper>

          <Paper
            elevation={0}
            sx={{
                borderBottom: '1px solid #AEC2B6',
              p: 2,
              margin: "auto",
              // maxWidth: 500,
              flexGrow: 1,
              backgroundColor: (theme) =>
                theme.palette.mode === "dark" ? "#1A2027" : "#fff",
            }}
          >
            <Grid container spacing={0}>
              <Grid item lg={4} sm={4} md={4} xs={4}>
                <ButtonBase>
                  <Typography variant="body1">Invite Link: </Typography>
                </ButtonBase>
              </Grid>
              <Grid item lg={8} sm={8} md={8} xs={8} sx={{ overflow: "hidden",textAlign: "right" }}>
                <ButtonBase>
                  <Typography variant="body1"  > 
                 {InviteLink?InviteLink:null}{profits?profits.invitaion_code:null}<ContentCopyIcon />
                  </Typography>
                </ButtonBase>
              </Grid>
            </Grid>
          </Paper>

           <ButtonCommon />
        </Paper>
      </Box> :null}
       
    </>
  )
}

export default ShareCode