import React, { useState, useEffect } from "react";
// import "./App.css";
// import "./styles/common.css";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Protected from "../utils/auth/index";
import Main from "../pages/Client/Main";
import Signup from "../pages/Client/Signup";
import Login from "../pages/Client/Login";
import AdminMain from "../pages/Admin/AdminMain";
import AdminLogin from "../pages/Admin/AdminLogin";
import NotFound from "../pages/NotFound";
import UserCenter from "../components/UserCenter";
import Recharge from "../components/Recharge";
import TeamReport from "../components/TeamReport";
import Withdraw from "../components/Withdraw";
import ShareCode from "../components/ShareCode";
import MyIncome from "../components/MyIncome";
import Dashboard from "../components/Dashboard";
import Activities from "../components/Activities";

import AdminMainDeposit from "../pages/Admin/AdminMainDeposit";
import AdminMainWithdraw from "../pages/Admin/AdminMainWithdraw";
import ActivityView from "../pages/Admin/ActivityView";
function Index() {
    return (
        <>
            <Router>
                <Routes>
                    <Route exact path="/signup" element={<Signup />} />
                    <Route exact path="/login" element={<Login />} />
                    {/* <Route
                        exact
                        path="/"
                        element={<Protected Component={Main} />}
                    /> */}
                    {/* /Admin/login */}

                    <Route
                        exact
                        path="/Admin/login"
                        element={<AdminLogin />}
                    />
                    <Route
                        exact
                        path="/Admin/"
                        element={<Protected Component={AdminMain} check={true} />}
                    />
                    <Route exact path="/"  element={<Protected Component={Dashboard} check="false"/>}  />
                    <Route exact path="/usercenter"  element={<Protected Component={UserCenter} check="false"/>}  />
                    <Route exact path="/recharge" element={<Protected Component={Recharge} check="false"/>}  />
                    <Route exact path="/teamreport"  element={<Protected Component={TeamReport} check="false"/>}  />
                    <Route exact path="/withdraw" element={<Protected Component={Withdraw} check="false"/>}  /> 
                    <Route exact path="/sharecode"  element={<Protected Component={ShareCode} check="false"/>}  /> 
                    <Route exact path="/myincome" element={<Protected Component={MyIncome} check="false"/>}  />
                    <Route exact path="/dashboard" element={<Protected Component={Dashboard} check="false"/>}   />
                    <Route exact path="/activities" element={<Protected Component={Activities} check="false"/>}   />
                    <Route
                        exact
                        path="/Admin/deposit"
                        element={<Protected Component={AdminMainDeposit} check={true} buttomIndex={1} />}
                    />
                    <Route
                        exact
                        path="/Admin/ActivityView/:id"
                        element={<Protected Component={ActivityView} check={true} buttomIndex={1} />}
                    />

                    <Route
                        exact
                        path="/Admin/withdraw"
                        element={<Protected Component={AdminMainWithdraw} check={true} buttomIndex={1} />}
                    />
                    <Route exact path="/usercenter" element={<UserCenter />} />
                    <Route exact path="/recharge" element={<Recharge />} />
                    <Route exact path="/teamreport" element={<TeamReport />} />
                    <Route
                        exact
                        path="*"
                        element={<NotFound />}
                    />
                </Routes>
            </Router>
        </>
    );
}

export default Index;
