import React from 'react'
import { Button, Paper } from '@mui/material'
import Cookies from "js-cookie";
import { useNavigate } from 'react-router'; 

const ButtonCommon = () => {
    const navigate = useNavigate();
    const cookieHandle = () => {
        Cookies.remove('asdae3wd3ew');
        Cookies.remove('da3sace4wsd');
        navigate("/login");
    }
  return (
    <>
 <Paper elevation={0} sx={{ 
      marginTop: { xs: 3, sm: 7, md: 5, lg: 5  },
      marginBottom: { xs: 3, sm: 7, md: 5, lg: 5  },
      marginLeft: { xs: 4, sm: 7, md: 5, lg: 5  },
     }}>   
      <Button  variant="outlined"  onClick={cookieHandle}
      sx={{ borderRadius:"20px", width:"90%",}} fontFamily= "Audiowide" sans-serif>Sign out</Button>
      </Paper>  
    </>
  )
}

export default ButtonCommon