import React, { useEffect, useState } from "react";
import Box from "@mui/material/Box";
import { styled } from "@mui/material/styles";
import Paper from "@mui/material/Paper";
import { Button, Fab, Grid, Input, TextField, Typography } from "@mui/material";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import GroupAddIcon from "@mui/icons-material/GroupAdd";
import PeopleAltIcon from "@mui/icons-material/PeopleAlt";
import PaidIcon from "@mui/icons-material/Paid";
import SendIcon from "@mui/icons-material/Send";
import Stack from "@mui/material/Stack";
import KeyboardArrowRightIcon from "@mui/icons-material/KeyboardArrowRight";
import KeyboardBackspaceIcon from "@mui/icons-material/KeyboardBackspace";
import ButtonBase from "@mui/material/ButtonBase"; 
import axios from "axios";
import { config } from "./Config/Config";
import { NavLink } from "react-router-dom";
import ButtonCommon from "./ButtonCommon";

const ariaLabel = { 'aria-label': 'description' };

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === "dark" ? "#1A2027" : "#fff",
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: "center",
  color: theme.palette.text.secondary,
}));


const Withdraw = () => {

  const [profits, setProfit] =useState();
console.log(profits , "state withdraw ")

  const [values, setValues] = useState({ 
    amount: '',
    password: ''
  });

  const handleChange = name => event => {
    setValues({ ...values, [name]: event.target.value });
  };

  const postForm = () => {
    const configg = {
      headers: {
        'content-type': 'application/json',
      },
    };
    const url = config.baseURL + '/actitivty/withdraw';
    console.log(url);

    axios
    .post(
      url,
      { 
        customerId: localStorage.getItem('customerId'),
        amount: values.amount,
        password: values.password
       },
       configg
      )
      .then((response) => {
        console.log(response, 'Response check');
        if (response.status === 200) {
            console.log(response, 'Submit Successfully'); 
        } if (response.status === 403) {
          console.log(response, 'Please enter correct password'); 
      }
      })
      .catch((e) => {
    
        console.log(e.message);
      });
  };


const handleSubmit = (event) => {
    event.preventDefault();
    postForm();
    // const data = new FormData(event.currentTarget); 
};


  const getProfit = () => {
    let url = config.baseURL + "/customer?id=8";
    console.log( url);

    axios
    .get(url)
      .then((response) => {
        if (response.status == 200) {
            console.log(response, "response profit withdraw");
            // console.log(response.data, "get profit");
            setProfit(response.data.response);
        }
      })
      .catch((e) => {
        console.log(e);
      });
  };
  // const totalBalance = profits.profit + profits.asset; 

  useEffect(()=>{
    getProfit();
  }, [])

  return (
    <>
      <Box
        sx={{
          justifyContent: "center",
          alignItems: "center",
          maxHeight: "100vh",
          display: "flex",
          flexWrap: "wrap",
          paddingBottom: '5px',
          "& > :not(style)": {
            m: 1,
            width: 900,
            // height: 600,
          },
        }}
      >
        <Paper elevation={9}>
          <Paper
            elevation={0}
            sx={{
              p: 2,
              margin: "auto",
              // maxWidth: 500,
              flexGrow: 1,
              backgroundColor: (theme) =>
                theme.palette.mode === "dark" ? "#1A2027" : "#fff",
            }}
          >
            <Grid container spacing={2} sx={{ mt: 2 }}>
              <Grid item xs={5} sm={5} md ={5} lg={5}>
                <ButtonBase>
                  <NavLink to="/usercenter"><KeyboardBackspaceIcon /></NavLink>
                  
                </ButtonBase>
              </Grid>
              <Grid item xs={7} sm={7} md ={7} lg={7}>
              <ButtonBase>
                  <Typography variant="h5" sx={{ textAlign: "center" }}>
                    Withdraw
                  </Typography>
              </ButtonBase>
                </Grid> 
            </Grid>
          </Paper>

          <Paper xs={12} sm={12} md={12} lg={12}
            elevation={5}
            sx={{
              m: 3,
              borderRadius: "10px",
              p: 3,
            }}
          >
            <Typography sx={{ fontWeight: "bolder", color: "blue" }}>
              {profits?profits.profit:null}
            </Typography>
            <Typography sx={{ mt: 1 }}>Current Balance</Typography>
          </Paper>

          <Box  
          component="form"
         noValidate
        onSubmit={handleSubmit}
        >
          <Paper
            elevation={0}
            sx={{
                borderBottom: '1px solid #AEC2B6',
              px: 2,
              margin: "auto",
              // maxWidth: 500,
              flexGrow: 1,
              backgroundColor: (theme) =>
                theme.palette.mode === "dark" ? "#1A2027" : "#fff",
            }}
          >
            <Grid container spacing={0} sx={{mb:2}}>
              <Grid item xs={4} sm={4} md={4} lg={4}> 
                <ButtonBase>
                  <Typography variant="body1">Wallet Type:</Typography>
                </ButtonBase>
              </Grid>
              <Grid item xs={8} sm={8} md={8} lg={8} sx={{ textAlign: "right" }}> 
              <ButtonBase>
                  <Typography variant="body1" >
                    TRC20
                  </Typography> 
                  </ButtonBase>
              </Grid>
            </Grid>
          </Paper>

          <Paper
            elevation={0}
            sx={{
                borderBottom: '1px solid #AEC2B6',
              p: 2,
              margin: "auto",
              // maxWidth: 500,
              flexGrow: 1,
              backgroundColor: (theme) =>
                theme.palette.mode === "dark" ? "#1A2027" : "#fff",
            }}
          >
            <Grid container spacing={0}>
              <Grid item lg={4}>
                <ButtonBase>
                  <Typography variant="body1">Wallet address: </Typography>
                </ButtonBase>
              </Grid>
              <Grid item lg={8} sx={{ overflow: "hidden" }} textAlign='right'>
                <ButtonBase>
                  <Typography variant="body1">
                  xsakjncksnklsc
                  </Typography>
                </ButtonBase>
              </Grid>
            </Grid>
          </Paper>

          <Paper
            elevation={0}
            sx={{
                borderBottom: '1px solid #AEC2B6',
              p: 2,
              margin: "auto",
              // maxWidth: 500,
              flexGrow: 1,
              backgroundColor: (theme) =>
                theme.palette.mode === "dark" ? "#1A2027" : "#fff",
            }}
          >
            <Grid container spacing={0}>
              <Grid item xs={3} sm={3} md={3} lg={3}>
                <ButtonBase>
                  <Typography variant="body1">Withdrawal Amount:</Typography>
                </ButtonBase>
              </Grid>
              <Grid item xs={9} sm={9} md={9} lg={9}> 
                  <Typography
                    variant="body1"
                    sx={{ textAlign: "right", color: "blue" }}
                  >
                     <Input inputProps={ariaLabel } sx={{input: {textAlign: "right",}, }} fullWidth 
                     name="amount"
                     value={values.amount}
                     type="text"
                     onChange={handleChange('amount')}
                     placeholder="30$"
                     />
                  </Typography>
              
              </Grid>
            </Grid>
          </Paper>

          <Paper
            elevation={0}
            sx={{
                borderBottom: '1px solid #AEC2B6',
              p: 2,
              margin: "auto",
              // maxWidth: 500,
              flexGrow: 1,
              backgroundColor: (theme) =>
                theme.palette.mode === "dark" ? "#1A2027" : "#fff",
            }}
          >
            <Grid container spacing={0} >
              <Grid item lg={4}>
                <ButtonBase>
                  <Typography variant="body1">Fund password:</Typography>
                </ButtonBase>
              </Grid>
              <Grid item lg={8} sx={{ overflow: "hidden",textAlign: "right" }} >
                <Typography variant="body1" sx={{}}>
                <Input inputProps={ariaLabel } sx={{input: {textAlign: "right",}, }} fullWidth 
                 name="password"
                 value={values.password}
                 type="text"
                 onChange={handleChange('password')}
                />
                </Typography>
              </Grid>
            </Grid>
          </Paper>

          <Paper elevation={0} sx={{ mb: 1, mt: 4 }}>
            <Button
              type="submit"
              variant="outlined"
              sx={{
                borderRadius: "20px",
                width: "87%",
                ml: 5,
                background: "blue",
                color: "white",
                "&:hover": {
                  color: "black",
                },
              }}
            >
              Confirm withdrawal
            </Button>
          </Paper>
          </Box>

          <Box sx={{ flexGrow: 1 }}>
            <Grid container spacing={0}>
              <Grid item xs>
                <Item
                  variant="h6"
                  sx={{ textAlign: "start", px: 3, color: "blue" }}
                >
                  Withdrawal Instructions
                </Item>
              </Grid>


              <Grid item sx>
                <Item sx={{ textAlign: "start", px: 3 }}>
                  1. Please confirm whether Wallet address: is correct. If the
                  information is incorrectly filled in, we will not be
                  responsible for any loss of your insurance. <br />
                  2. The withdrawal amount will be processed within 2 to 72
                  hours. <br />
                  3. You can only apply for withdrawals 1 times per day.{" "}
                </Item>
              </Grid>
            </Grid>
          </Box>
          <ButtonCommon />
        </Paper>
      </Box>
    </>
  );
};

export default Withdraw;
