import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";
// import { useSelector } from "react-redux";
import Cookies from "js-cookie";

const Protected = (props) => {
    // const ialvaliduser = useSelector(state => state.protect)
    const protectionCookieClient = Cookies.get("da3sace4wsd")
    const protectionCookieAdmin = Cookies.get("asdae3wd3ew")
    let { Component, check } = props;
    const navigate = useNavigate();
    useEffect(() => {
        window.scrollTo({
            top: 0,
            behavior: "instant",
        });
        // console.log("passed component", Component);

        // let checkAuth = localStorage.getItem("ValidUser");
        // console.log("checkAuth protection", checkAuth);
        // navigate("/login");
        if (check == true) {
            console.log("for admin")
            if (Boolean(protectionCookieAdmin) == false || Boolean(protectionCookieAdmin) == null || Boolean(protectionCookieAdmin) == undefined) {
                // console.log("in if condition  go to login", ialvaliduser);
                navigate("/Admin/login");
            } else {
                navigate("/admin");

            }

        }
        else {
            console.log("for client");
            if (Boolean(protectionCookieClient) == false || Boolean(protectionCookieClient) == null || Boolean(protectionCookieClient) == undefined) {
                // console.log("in if condition  go to login", ialvaliduser);
                navigate("/login");
            } else {
                navigate("/");

            }
        }
    }, []);
    return (
        <div>
            <Component />
        </div>
    )
}

export default Protected