import React, { useEffect, useState } from 'react'
import Box from '@mui/material/Box';
import { styled } from '@mui/material/styles';
import Paper from '@mui/material/Paper';
import { Button, Fab, Grid, Typography } from '@mui/material';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import GroupAddIcon from '@mui/icons-material/GroupAdd'; 
import PeopleAltIcon from '@mui/icons-material/PeopleAlt';
import PaidIcon from '@mui/icons-material/Paid';
import SendIcon from '@mui/icons-material/Send';
import Stack from '@mui/material/Stack';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import KeyboardBackspaceIcon from '@mui/icons-material/KeyboardBackspace';
import ButtonBase from '@mui/material/ButtonBase';  
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody'; 
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow'; 
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import { NavLink } from 'react-router-dom';
import { config } from './Config/Config';
import axios from 'axios';
import ButtonCommon from './ButtonCommon';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
      backgroundColor: theme.palette.common.black,
      color: theme.palette.common.white,
    },
    [`&.${tableCellClasses.body}`]: {
      fontSize: 14,
    },
  }));
  
  const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
    // hide last border
    '&:last-child td, &:last-child th': {
      border: 0,
    },
  }));

const Activities = () => {
    const [Profits, setProfit] =useState();
  console.log(Profits , "state in activity")

  const [teamMember, setTeamMember] =useState([]);
  console.log(teamMember , "profit state in team Member")

    const getProfitTeam = () => {
      let url = config.baseURL + "/customer?id=8";
      console.log( url);
  
      axios
      .get(url)
        .then((response) => {
          if (response.status == 200) {
              console.log(response, "response in activity");
              // console.log(response.data, "get profit");
              setProfit(response.data.response);
          }
        })
        .catch((e) => {
          console.log(e);
        });
    };

    const getActivityList = () => {
      let url = config.baseURL + "/listActitivtybyCustomer?id=8";
      console.log( url);
  
      axios
      .get(url)
        .then((response) => {
          if (response.status == 200) {
              console.log(response, "get response in Team Member");
              // console.log(response.data, "get profit");
              setTeamMember(response.data.response);
          }
        })
        .catch((e) => {
          console.log(e);
        });
    };

    useEffect(()=>{
      getProfitTeam();
      getActivityList();
    }, [])
  return (
    <>
        {teamMember && Profits?  <Box
      sx={{ 
        justifyContent:"center",
        alignItems:"center",
        maxHeight:"100vh",
        display: 'flex',
        flexWrap: 'wrap', 
        '& > :not(style)': {
          m: 1,
          width: 900,
          // height: 600,
        },
      }}
    >
     
      <Paper elevation={9} sx={{pb:5}}>
    
      <Paper elevation={0} 
      sx={{
        p: 2, 
        margin: 'auto',
        maxWidth: 500,
        flexGrow: 1,
        backgroundColor: (theme) =>
          theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
      }}
    >
      <Grid container spacing={2} sx={{mt:2}}>
        <Grid item xs={3} sm={3} md={3} lg={3}>
          <ButtonBase>
            <NavLink to="/usercenter"><KeyboardBackspaceIcon /></NavLink>
             
          </ButtonBase>
        </Grid>
        <Grid item xs={6} sm={6} md={9} lg={9}> 
            <Grid item xs> 
              <Typography variant="h5" sx={{textAlign:"center", }}>
                Activity
              </Typography> 
          </Grid> 
        </Grid>
      </Grid>
    </Paper>
    
    
      <Paper elevation={5}   sx={{
    m:3, 
    borderRadius:"10px",
    p:3
    }}>
         <Grid container spacing={0}>
            <Grid item  xs={6} sm={6} md={6} lg={6} sx={{textAlign:"center"}}> 
            <Typography sx={{fontWeight:"bolder" }}>
        {Profits?Profits.profit:null}
      </Typography>
      <Typography sx={{mt:1}}>
        Your profits
      </Typography>
            </Grid>
            {/* <Grid item md={4} sx={{textAlign:"center"}}> 
            <Typography sx={{fontWeight:"bolder"}}>
        {Profits?Profits.totalAsset:null} 
      </Typography>
      <Typography sx={{mt:1}}>
        Team assets 
      </Typography>
            </Grid> */}
            <Grid item xs={6} sm={6} md={6} lg={6} sx={{textAlign:"center"}}> 
            <Typography sx={{fontWeight:"bolder" }}>
            {Profits?Profits.asset:null} 
      </Typography>
      <Typography sx={{mt:1}}>
        Your assets
      </Typography>
            </Grid>
          </Grid>
    
          {/* <Grid container spacing={0}>
            <Grid item md={6} sx={{textAlign:"center"}}> 
            <Typography sx={{fontWeight:"bolder" }}>
        0
      </Typography>
      <Typography sx={{mt:1}}>
        Active people today
      </Typography>
            </Grid>
            <Grid item md={6} sx={{textAlign:"center"}}> 
            <Typography sx={{fontWeight:"bolder"}}>
        0
      </Typography>
      <Typography sx={{mt:1}}>
        New people
      </Typography>
            </Grid> 
          </Grid>  */}
      </Paper> 
    
      {/* <Paper elevation={5}   sx={{
    m:3, 
    borderRadius:"10px",
    p:3
    }}>
         <Grid container spacing={0}>
            <Grid item md={4} sx={{textAlign:"center"}}> 
            <Typography sx={{  }}>
            LV1(0)
      </Typography> 
            </Grid>
            <Grid item md={4} sx={{textAlign:"center"}}> 
            <Typography sx={{ }}>
            LV2(0)
      </Typography>
            </Grid>
            <Grid item md={4} sx={{textAlign:"center"}} > 
            <Typography sx={{ }}>
            LV3(0)
      </Typography> 
            </Grid> 
          </Grid> 
      </Paper>  */}
      <TableContainer component={Paper} >
          <Table sx={{ }} aria-label="customized table">
            <TableHead>
              <TableRow>
                <StyledTableCell  align="center">Name </StyledTableCell>
                <StyledTableCell  align="center" >Date </StyledTableCell>
                <StyledTableCell   align="center">Type </StyledTableCell>
                <StyledTableCell  align="center">Status </StyledTableCell>  
              </TableRow>
            </TableHead>
            <TableBody>
              {teamMember?.map((value, index) => (
                <StyledTableRow key={index} sx={{}} >
                  <StyledTableCell align="center"> {value?value.customer_name:null} </StyledTableCell>
                  <StyledTableCell align="center">{value?value.create_date.slice(0,10):null}</StyledTableCell>
                  <StyledTableCell align="center">{value.activity_type===1 ? "Deposit": "Withdraw"}</StyledTableCell>
                  <StyledTableCell align="center">{value.status=== 1 ? 'Request' : value.status === 2 ? 'Approved' : 'Decline'}</StyledTableCell> 
                </StyledTableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
    
    <ButtonCommon />
      </Paper> 
      
    </Box>  :null}
    </>
  )
}

export default Activities