import React, { useEffect, useState } from 'react'
import Box from '@mui/material/Box';
import { styled } from '@mui/material/styles';
import Paper from '@mui/material/Paper';
import { Button, Fab, Grid, Input, Typography } from '@mui/material';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import GroupAddIcon from '@mui/icons-material/GroupAdd'; 
import PeopleAltIcon from '@mui/icons-material/PeopleAlt';
import PaidIcon from '@mui/icons-material/Paid';
import SendIcon from '@mui/icons-material/Send';
import Stack from '@mui/material/Stack';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import KeyboardBackspaceIcon from '@mui/icons-material/KeyboardBackspace';
import ButtonBase from '@mui/material/ButtonBase';  
import axios from 'axios';
import { config } from './Config/Config';
import { NavLink } from 'react-router-dom';
import Snackbar from '@mui/material/Snackbar';
import ButtonCommon from './ButtonCommon';
import "../Styles/Recharge.css";
import FileUploadIcon from '@mui/icons-material/FileUpload';

const ariaLabel = { 'aria-label': 'description' };


const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

const Recharge = () => { 

  const [state, setState] = React.useState({
    open: false,
    vertical: 'top',
    horizontal: 'center',
  });
  const { vertical, horizontal, open } = state;

  const handleClick = (newState) => () => {
    setState({ open: true, ...newState });
  };

  const handleClose = () => {
    setState({ ...state, open: false });
  };
  const [profits, setProfit] =useState(); 
 console.log(profits, "profits state")

  const [values, setValues] = useState({ 
    customerId: localStorage.getItem("customerId"),
    amount: localStorage.getItem("amount"),
    img:""
  }); 

  const imageupload = (event) => {
    console.log(event.target.files[0]);
    setValues({
      ...values,
      img: event.target.files[0],
    });
  };
    const handleinput=(e) => {
    console.log( "handle inoput");
    console.log( e.target.value );
    setValues({ ...values, "amount": e.target.value});
    localStorage.setItem("amount", e.target.value);
  };
  // const handleChange=(e) => {
  //   console.log( "handle inoput");
  //   console.log( e.target.value );
  //   setValues({ ...values, [e.target.name]: e.target.value });
  // };

  const postForm = () => { 
      console.log( "Run postForm "); 
      const formdata = new FormData();
        formdata.append("customerId", values.customerId);
        formdata.append("amount", values.amount);
        formdata.append("img", values.img);
        

      const configg = {
        headers: {
          "content-type": "multipart/form-data",
        },
      };
      let url = config.baseURL + "/actitivty/deposit";
      console.log( url);

      axios
      .post(
          url,
          formdata,
          configg
        )
        .then((response) => {
          if (response.status == 200) {
              console.log(response, "submit succesfully");  
          }
        })
        .catch((e) => {
          console.log(e);
        });
        
    };
    const handleSubmit = (event) => {
      event.preventDefault();
      postForm();
      // const data = new FormData(event.currentTarget); 
  };

 
  const getProfit = () => {
    let url = config.baseURL + "/customer?id=8";
    console.log( url);

    axios
    .get(url)
      .then((response) => {
        if (response.status == 200) {
            console.log(response.data.response, "response profits");
            // console.log(response.data, "get profit");
            setProfit(response.data.response);
        }
      })
      .catch((e) => {
        console.log(e);
      });
  };
  // const totalBalance = profits.profit ? (JSON.parse(profits.profit) + JSON.parse(profits.asset)):null; 

  useEffect(()=>{
    getProfit();
  }, [])

  return (
    <>{profits?<Box
      sx={{ 
        justifyContent:"center",
        alignItems:"center",
        maxHeight:"100vh",
        display: 'flex',
        flexWrap: 'wrap',
        '& > :not(style)': {
          m: 1,
          width: 900,
          // height: 600,
        },
      }}
    >
     
      <Paper elevation={9} >
 
      <Paper elevation={0} 
      sx={{
        p: 2, 
        margin: 'auto',
        // maxWidth: 500,
        flexGrow: 1,
        backgroundColor: (theme) =>
          theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
      }}
    >
      <Grid container spacing={2} sx={{mt:2}}>
        <Grid item xs={3} sm={3} md={3} lg={3}>
          <ButtonBase>
           <NavLink to="/usercenter"><KeyboardBackspaceIcon /></NavLink>  
          </ButtonBase>
        </Grid>
        <Grid item xs={6} sm={6} md={6} lg={6}> 
            <Grid item xs> 
              <Typography variant="h5" sx={{textAlign:"center", }}>
                Recharge
              </Typography> 
          </Grid> 
        </Grid>
      </Grid>
    </Paper>

   
      <Paper elevation={5}   sx={{
    m:3, 
    borderRadius:"10px",
    p:3
  }}>
     <Typography sx={{fontWeight:"bolder", color:"blue"}}> 
        {  profits.asset}
      </Typography>
      <Typography sx={{mt:1}}>
        Current Balance
      </Typography>
      </Paper> 

      <Box  
          component="form"
         noValidate
        onSubmit={handleSubmit}
        >
      <Paper elevation={0} 
      sx={{
        px: 2, 
        my: 5,
        // margin: 'auto',
        // maxWidth: 500,
        flexGrow: 1,
        backgroundColor: (theme) =>
          theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
      }}
    >
      <Grid container spacing={0} >
        <Grid item xs={6} sm={6} md={6} lg={6}>
          <ButtonBase>
             <Typography variant="h6">Recharge Type</Typography>
          </ButtonBase>
        </Grid>
        <Grid item xs={6} sm={6} md={6} lg={6}> 
            <Grid item xs> 
              <Typography variant="h6" sx={{textAlign:"right", }}>
            TRC20
              </Typography> 
          </Grid> 
        </Grid>
      </Grid>
    </Paper>

    <Paper elevation={0} 
      sx={{
        px: 2,
        my: 5,
        // margin: 'auto',
        // maxWidth: 500,
        flexGrow: 1,
        backgroundColor: (theme) =>
          theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
      }}
    >
      <Grid container spacing={0} >
        <Grid item xs={7} sm={7} md={7} lg={7}>
          <ButtonBase>
             <Typography variant="h6">Recharge amount</Typography>
          </ButtonBase>
        </Grid>
        <Grid item xs={5} sm={5} md={5} lg={5}> 
            <Grid item xs> 
              <Typography variant="h6" sx={{textAlign:"right", color:"blue" }}>
              <Input inputProps={ariaLabel } sx={{input: {textAlign: "right",}, }} fullWidth 
                     name="amount"
                     value={values.amount}
                     type="text"
                     onChange={handleinput}
                     placeholder="30$"
                     />
              </Typography> 
          </Grid> 
        </Grid>
      </Grid>
    </Paper>
    <Paper elevation={0} sx={{ 
      margin: { xs: 3, sm: 5, md: 5, lg: 5 }
     }} >  
    <Grid container sx={{ textAlign: 'center' }}>
      <Grid item xs={4} sm={4} md={4} lg={4}>
      <Fab variant="extended" type='input' value="30"  onClick={handleinput} name="amount" size='small' sx={{
        width: { xs: 80, sm: 150, md: 150, lg: 150 }
      }}  >30$  </Fab>

      </Grid>
      <Grid item xs={4} sm={4} md={4} lg={4}>
      <Fab variant="extended" type='input' size='small' value="100" onClick={handleinput} name="amount"  sx={{
        width: { xs: 80, sm: 150, md: 150, lg: 150 }
      }}>100$  </Fab>

      </Grid>
      <Grid item xs={4} sm={4} md={4} lg={4}>
      <Fab variant="extended" type='input' size='small' value="1000" onClick={handleinput} name="amount" sx={{
        width: { xs: 80, sm: 150, md: 150, lg: 150 }
      }}>1000$  </Fab>

      </Grid>
    </Grid>
      </Paper> 

    <Paper elevation={0} 
      sx={{
        p: 2, 
        margin: 'auto',
        // maxWidth: 500,
        flexGrow: 1,
        backgroundColor: (theme) =>
          theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
      }}
    >
      <Grid container spacing={0} >
        <Grid item xs={6} sm={6} md={6} lg={6}>
          <ButtonBase>
             <Typography variant="body1" sx={{mt:1}}>Upload screenshot</Typography>
          </ButtonBase>
        </Grid>
        <Grid item xs={6} sm={6} md={6} lg={6}>   
<Button
  variant="contained"
  component="label"
>
<FileUploadIcon />Upload File
  <input inputProps={ariaLabel } fullWidth 
    required
    type="file"
    onChange={(e)=>imageupload(e)}
    name="img"
    hidden
  />
</Button>
        </Grid>
      </Grid>
    </Paper>

    

      <Box sx={{ flexGrow: 1 }}>
      <Grid container spacing={0} >
        <Grid item xs>
          <Item variant='h6' sx={{textAlign:"start", px:3, color:"blue"}}>Recharge Instructions</Item>
        </Grid>
        <Grid item sx>
          <Item sx={{textAlign:"start", px:3}}>1. The payment amount must be the same as the order amount, otherwise it will not be automatically credited to the account.</Item>
        </Grid>
        <Grid item xs>
          <Item sx={{textAlign:"start", px:3}}>2. If the deposit and withdrawal are not received, please consult your superior for other questions</Item>
        </Grid>
      </Grid>
    </Box>
    
    <Paper elevation={0}  sx={{ 
      marginTop: { xs: 3, sm: 7, md: 5, lg: 5  },
      marginBottom: { xs: 3, sm: 7, md: 5, lg: 5  },
      marginLeft: { xs: 4, sm: 7, md: 5, lg: 5  },
     }} >   
      <Button type="submit"  variant="outlined"  sx={{ borderRadius:"20px", width:"90%", background:"blue", color:"white", '&:hover': {
    color: "black",
  },}}  onClick={handleClick({
    vertical: 'top',
    horizontal: 'center',
  })}> Confirm recharge</Button> <Snackbar
  anchorOrigin={{ vertical, horizontal }}
  open={open}
  onClose={handleClose}
  message="Successfully Submit"
  key={vertical + horizontal}
/>
    </Paper> 
</Box>

<ButtonCommon />
      </Paper> 
    </Box> : null}
         
    </>
  )
}

export default Recharge